import React, { useEffect, useState } from 'react';
import { Button, Card, CardContent, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import LibraryAddIcon from '@material-ui/icons/LibraryAdd';
import EditIcon from '@material-ui/icons/Edit';
import { useRecipesValue } from '../context/RecipesContext';
import { firebase } from '../firebase';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';

const useStyles = makeStyles((theme) => ({
  details: {
    display: 'flex',
    width: '100%',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  content: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    paddingBottom: '16px !important',
  },
  cover: {
    width: 45,
    height: 45,
    backgroundSize: 'contain',
    marginRight: '10px',
    color: '#43a047',
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
  playIcon: {
    height: 38,
    width: 38,
  },
  marginRight: {
    marginRight: 10,
  },
  primaryColor: {
    color: 'rgba(0, 0, 0, 0.60)',
  },
}));

const ITEM_HEIGHT = 48;

export const MenuItemCard = ({ name, itemsList, docId }) => {
  const [included, setIncluded] = useState(false);
  const { recipes, setRecipes } = useRecipesValue(false);
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  // const handeFoodInclude = () => {
  //   if (!included) {
  //     setIncluded(true);
  //   } else {
  //     setIncluded(false);
  //   }
  // };

  const deleteRecipe = () => {
    firebase
      .firestore()
      .collection('recipes')
      .doc(docId)
      .delete()
      .then(() => {
        setRecipes([...recipes]);
        handleClose();
      });
  };

  useEffect(() => {
    // isFoodAvailable() ? setIncluded(true) : setIncluded(false);
    console.log(itemsList);
    console.log(docId);
  }, []);

  return (
    <Card className='menu-card'>
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <MenuBookIcon className={classes.cover} />
          <Typography component='h6' variant='h6'>
            {name}
          </Typography>
        </CardContent>
        <div className='menu-card__button'>
          <IconButton
            aria-label='more'
            aria-controls='long-menu'
            aria-haspopup='true'
            onClick={handleClick}
          >
            <MoreVertIcon />
          </IconButton>
          <Menu
            id='long-menu'
            anchorEl={anchorEl}
            keepMounted
            open={open}
            onClose={handleClose}
            PaperProps={{
              style: {
                maxHeight: ITEM_HEIGHT * 4.5,
                display: 'flex',
                justifyContent: 'space-between',
              },
            }}
          >
            <MenuItem className={classes.primaryColor} onClick={handleClose}>
              <LibraryAddIcon color='primary' className={classes.marginRight} />{' '}
              Add To Menu
            </MenuItem>
            <MenuItem className={classes.primaryColor} onClick={handleClose}>
              <EditIcon color='primary' className={classes.marginRight} /> Edit
            </MenuItem>
            <MenuItem className={classes.primaryColor} onClick={deleteRecipe}>
              <DeleteForeverIcon
                color='primary'
                className={classes.marginRight}
              />
              Delete
            </MenuItem>
          </Menu>
        </div>
      </div>
    </Card>
  );
};
